    position: absolute;
    height: auto;
    width: 90%;
    max-width: 800px;
    left: 50%;
    top: 50%;
    padding: 50px 20px;
    border-radius: 4px;
    background: rgb(255, 255, 255);
    z-index: 5;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
