.tb_modal{
    font-family: 'Inter', sans-serif;
    font-size: 14px;
    line-height: 1.42857;
    color: #262626;
}
.tb_modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
    z-index: 999;
    opacity: 0;
    visibility: hidden;
    background: rgba(0, 0, 0, 0.7);
    display: -webkit-flex;
    transition: opacity 0.3s ease-out 0s;
}

.tb_modal.in{
    opacity: 1;
}

a:hover, a:active, a:focus {
    text-decoration: none;
}

button:focus {
    outline: none;
    outline: -webkit-focus-ring-color auto 0px;
}
.tb_modal.show {
  opacity:1;
  visibility: visible;
  transition: opacity 300ms ease-in-out;
}

.tb_fade {
    transition: opacity 0.15s linear;
}

.tb_modal_dialog.show {
    opacity:1;
    visibility: visible;
}

.tb_modal_dialog {
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
    min-height: 100%;
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    padding: 0;
    margin:0 auto;
    pointer-events: none;
    max-width: 90vw;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    transition-property: transform,-webkit-transform,opacity;
    transition-duration: .2s;
    transition-timing-function: ease-in;
    transition: opacity .3s,-webkit-transform .3s ease;
    transition: opacity .3s,transform .3s ease;
    transition: opacity .3s,transform .3s ease,-webkit-transform .3s ease;
    -moz-transition: opacity .3s,transform .3s ease;
    -webkit-transition: opacity .3s,transform .3s ease;
    -o-transition: opacity .3s,transform .3s ease;
    -webkit-transform: scale(0.4);
    transform: scale(0.4);
}

.tb_modal.show .tb_modal_dialog{
    opacity: 1;
    visibility: visible;
    -webkit-transform: scale(1);
    transform: scale(1);
    transition: all 0.3s;
}

.tb_conetent{
    position: relative;
    display: flex;
    flex-direction: column;
    pointer-events: auto;
    background-clip: padding-box;
    outline: 0;
    margin: 0 auto;
    transition: all 0.25s;
    position: relative;
    max-height: 100%;
    max-width: 100%;
    width: auto;
    justify-content: center;
    align-items: center;
}

.modal-only-text .tb_conetent {
    width: 100%;
}

.tb_modal_body {
    display: table;
    background-color: #fff;
    border-radius: 3px;
    overflow: hidden;
    position: relative;
    box-shadow: 0 0.25rem 0.5rem rgb(0 0 0 / 10%);
}

.tb_post_wrapper {
    padding: 15px 20px;
    height: calc(100% - 65px);
}

.modal-only-text .tb_post_wrapper {
    width: calc(40vw + 40vh);
}

.tb_post_wrap_in {
    height: 100%;
    display: block;
    width: 100%;
    overflow-y: auto;
}

.modal-only-text .mCSB_container {
    width: 100%;
