.selected-item {
    position:absolute; 
    width:100%; 
    height:100%; 
    top:0; 
    left:0; 
    opacity:1; 
    z-index:1000;
    transition: all 1.2s ease-in-out 0.7s;
}
.selected-item .image{
    background-color:white; 
    position:absolute; 
    border:#f3f3f3 10px solid; 
    max-width:none; 
    box-shadow: black 0px 0px 30px;
    -webkit-box-shadow: black 0px 0px 30px;
    -moz-box-shadow: black 0px 0px 30px;
    opacity:0; 
    /* width:100%; 
     height:100%; */
    width:auto !important; height:auto !important;
    min-width:350px;
    max-width:350px;
    /*max-height:350px;*/
    overflow:hidden;
    transition: all 1.2s ease-in-out 0.7s;
}


.content-outer{ 
    top:0 !important; 
    /*left:0; */
    /*width:0px; */
    left:350px !important;
    position:absolute; 
    overflow:hidden;
    transition: all ease 1s;
}
