#tweet-list .tweet:nth-child(4n) .image{
    -webkit-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
}
#tweet-list .tweet.show:nth-child(4n) .image{
    -webkit-transform: rotateY(360deg);
    transform: rotateY(360deg);
}
#tweet-list .tweet:nth-child(4n+1) .image{
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
}
#tweet-list .tweet.show:nth-child(4n+1) .image{
    -webkit-transform: rotateX(360deg);
    transform: rotateX(360deg);
}
#tweet-list .tweet:nth-child(4n+2) .image{
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
}
#tweet-list .tweet.show:nth-child(4n+2) .image{
    -webkit-transform: rotateX(360deg);
    transform: rotateX(360deg);
}
#tweet-list .tweet:nth-child(4n+3) .image{
    -webkit-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
}
#tweet-list .tweet.show:nth-child(4n+3) .image{
    -webkit-transform: rotateX(360deg);
    transform: rotateX(360deg);
}
#tweet-list .tweet .thumb{
    -webkit-perspective: 1000;
    -o-perspective: 1000;
    perspective: 1000;
    transition: all 1s ease-in-out;
}
#tweet-list .tweet.imageFlip .thumb{
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
    transition: all 0.1s ease-in-out;
}
 
#tweet-list .tweet .thumb img {
    width: 100%; 
