          animation: asd 0.5s 0.5s linear both;
  z-index: 2;
}

@-webkit-keyframes asd {
  0% {
    z-index: 2;
  }
  5% {
    z-index: 4;
  }
  100% {
    z-index: 4;
  }
}

@keyframes asd {
  0% {
    z-index: 2;
