button{
    cursor: pointer;
}
.onsite_submit_post_main{
    background-color: #e8e8e8;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1055;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    outline: 0;
    background-size: cover;
    background-repeat: no-repeat;
}

.onsite_submit_post_inner{
    max-width: 550px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    min-height: calc(100% - 1rem);
}
.submit_post_box{
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: 5px;
    outline: 0;
    overflow: hidden;
}
.submit_post_box .box_header{
    padding: 15px;
    text-align: center;
    padding-bottom: 0px;
}
.popup_logo{
    margin-top: 20px;
}
.popup_logo_img{
    max-width: 200px;
    object-fit: contain;
}
.submit_post_close_btn{
    position: absolute;
    right: 15px;
}
.submit_post_close_btn svg{
    cursor: pointer;
}
.box_title{
    text-align: center;
    padding-bottom: 15px;
}
.box_title .title{
    margin-bottom: 10px;
    font-size: 22px;
    font-weight: 700;
    margin-top: 15px;
}
.box_title .sub_heading{
    margin: 0px;
      font-size: 16px;
}
.box_body{
    background-color: #f8fafc;
    border-top: 1px solid #E5E5E5;
}
.box_body .tabs_nav{
    list-style: none;
    padding: 0px;
    margin: 0px;
    display: flex;
    justify-content: center;
    background-color: #fff;
}
.box_body .tabs_nav li{
    width: 50%;
    text-align: center;
    padding: 25px 15px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}
.box_body .tabs_nav li.active{
    background-color: #F0F2F3;
    color: #FF4050;
}
.submit_post_tabs{
    position: relative;
}
.submit_post_tabs .saprator{
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    background-color: #fff;
    padding: 10px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border-radius: 50%;
    border: 1px solid #F0F2F3;
}
.post_tabs_content{
    padding: 30px;
}
.post_tabs_content .image_upload_box{
    background-color: #fff;
    padding: 15px;
}
.add_caption_tab .image_upload_box{
    background-color: #fff;
    padding: 10px;
