}
.image_upload_box_content{
    background-color: #F0F2F3;
    text-align: center;
    height: 250px;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 5px;
    overflow: hidden;
}
.image_upload_box_content img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.image_upload_box_content .type_icon{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 30px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.image_upload_box_content .type_icon svg{
    width: 100%;
    height: 100%;
}
.image_upload_box_content .trash_icon{
    position: absolute;
