    right: 10px;
    background-color: #e9e9e9;
    border-radius: 50%;
    height: 30px;
    width: 30px;
    padding: 5px;
    cursor: pointer;
}
.image_upload_box_content .trash_icon svg{
    width: 100%;
    height: 100%;
