    border-radius: 3px;
    font-weight: 600;
    font-size: 16px;
    cursor: pointer;
    background-color: #FF4050 !important;
    color: #fff !important;
    outline: none;
    box-shadow: none !important;
}
.rating-stars{
    margin-bottom: 15px;
}
.rating-stars .react-stars span{
