}
.image_upload_box_content .uppy-DragDrop-inner::before{
    content: '';
    display: block;
    background: url(data:image/svg+xml;base64,);
    height: 22px;
    width: 25px;
    background-size: contain;
    background-repeat: no-repeat;
    margin: 0 auto;
    margin-bottom: 10px;
}
.uppy-DragDrop-container:focus{
