}

.btn-primary.submit_pic_spinner{
    padding-right: 3rem;
    opacity: 0.6;
    box-shadow: none;
    pointer-events: none;
}

.submit_pic_spinner:after {
    content: "";
    box-sizing: border-box;
    width: 1.4rem !important;
    display: inline-block;
    height: 1.4rem;
    vertical-align: middle;
    border-radius: 50%;
    border: 2px solid #ffffff;
    border-right: 2px solid transparent;
    -webkit-animation: submit_pic_animation-spinner 0.5s linear infinite;
            animation: submit_pic_animation-spinner 0.5s linear infinite;
    margin-left: 5px;
}

@-webkit-keyframes submit_pic_animation-spinner {
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
}
  
@keyframes submit_pic_animation-spinner {
    to {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
}


.btn-primary:not(.submit_pic_disabled):hover {
    background-color: #000;
}
.submit_pic_disabled{
    opacity: 0.6;
    box-shadow: none;
}
.submit_pic_limit_error{
    width: 100%;
    text-align: center;
    justify-content: center;
    margin-bottom: 10px;
}


